@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

Button {
    font-family: 'Mulish', sans-serif !important;
    font-weight: bolder !important;
    border-color: lightgrey !important; 
    border-width: 3px !important;
}

Button :hover {
    background-color: lightgrey !important;
}
/* Navbar styling */
.navbar {
    background-color: #ffffff;
    border: 1px solid #E2E5E6;
}

.nav {
    font-family: 'Mulish', sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 18px;
    color: #000000;
}

/* Inner navbar elements */
.nav-link {
    color: #373F41;
}
.navbar-form {
    background-color: #f4f5f4;
    border: 0px;
}

.navbar-logo {
    height: 40px;
}

.nav-join-waitlist-button {
    color: #3B8686;
    border-radius: 10px;
}

.nav-join-waitlist-button:focus {
    box-shadow: none !important;
}

.navbar-toggler {
    border: 0px;
}

.nav .toggle-button-group .toggle-button {
    color: #373F41;
}

@media only screen and (max-width: 768px) {
    .navbar {
        display: flex;
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        left: 0;
        right: 0;
        z-index: 1000;
    }

    .nav-join-waitlist-button {
        margin-bottom: 10px;
        width: 60%;
        margin-right: auto;
        font-size: 20px;
    }

    .navbar-collapse {
        text-align: left;
        width: 80%;
    }

    .nav-link {
        font-size: 20px;
        color: #373F41;
    }
}
.toggle-button {
    border: 1px;
    font-size: 14px;
    border: 0.5px solid #c4c4c4;
}

.toggle-button:focus {
    outline: none;
    box-shadow: none;
}

.left {
    border-radius: 5px 0px 0px 5px;
    border-right: none;
}

.right {
    border-radius: 0px 5px 5px 0px;
    border-left: none;
}


.song-card {
    font-family: 'Mulish', sans-serif;
    letter-spacing: 0.2px;
    color: #373f41;
    width: 18rem;
}

.songcard-title {
    font-weight: 900;
    font-size: 16px;
    line-height: 18px;
    color: #373f41;
}

.songcard-currency {
    font-size: 10px;
    line-height: 20px;
    color: #373f41;
}

.songcard-artist {
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    color: #373f41;
}

.songcard-listens {
    font-size: 10px;
    line-height: 18px;
    font-style: italic;
    color: #373f41;
}

.songcard-arrow {
    margin-top: 5px;
    margin-left: 5px;
    width: 14px;
    height: 8px;
}

.songcard-button {
    background-color: #ffffff;
    color: #373f41;
    font-size: 14px;
    line-height: 18px;
    border: 0px;
}

.songcard-button:hover {
    background-color: #373f41;
    color: #ffffff;
}
.info-dropdown {
