.footer-container {
    background-color: #2C3436;
    width: 100%;
    padding: 0;
    margin: 0;
}

.footer-inner-container {
    padding-top: 5%;
    padding-bottom: 5%;
    padding-left: 10%;
    padding-right: 10%;
    margin: 0;
    max-width: none;
}

.footer-text-wrapper {
    padding-top: 25px;
    padding-bottom: 25px;
    padding-left: 40px;
    padding-right: 40px;
    border-radius: 10px;
    margin-bottom: 50px;
    background-size: cover;
    margin-left: 0;
    margin-right: 0;
    max-width: none;
}

.footer-title {
    font-weight: 900;
    font-size: 25px;
    color: #ffffff;
    text-align: left;
    margin-bottom: 20px;
}

.footer-subtitle {
    font-weight: 600;
    font-size: 20px;
    color: white;
    text-align: left;
}

.footer-logo {
    width: 90%;
    height: auto;
}

.footer-col-right {
    float: right;
    text-align: right;
}

.footer-col-left {
    float: left;
    text-align: left;
}

.footer-button {
    font-weight: 900;
    font-size: 18px;
    border-radius: 10px;
}

.footer-button:focus {
    box-shadow: none !important;
}

.footer-link {
    font-weight: 900;
    font-size: 20px;
    color: #3B8686;
    transition: opacity 300ms;
    transition-timing-function: ease-out;
}

.footer-link:hover {
    opacity: 65%;
    color: #3B8686;
    text-decoration: none;
}

.footer-copyright {
    margin-top: 20px;
    font-size: 12px;
    font-weight: 600;
    color: #ffffff;
}

.footer-text-wrapper-mobile {
    display: none;
}

.footer-col-left .toggle-button-group .toggle-button {
    color: #ffffff
}

@media only screen and (max-width: 800px) {
    .footer-title {
        font-size: 18px;
    }

    .footer-subtitle {
        font-size: 16px;
    }

    .footer-row {
        text-align: center;
    }

    .footer-col-left {
        padding-bottom: 20px;
    }

    .footer-col-right {
        float: right;
        text-align: center;
    }

    .footer-copyright {
        text-align: center;
    }

    .footer-text-wrapper {
        display: none;
    }

    .footer-text-wrapper-mobile {
        padding: 20px;
        border-radius: 10px;
        margin-bottom: 50px;
        display: block;
        background-size: cover;
    }

    .fan-artist-toggle {
        padding-top: 20px;
    }
}

